.card-list{

    width:15%;
    height: 12px;
    margin-right:50px;
    margin-left:1em;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 48px 1.1em;
    
    
}
@media (max-width: 600px) {
    .card-list {
        grid-gap: 1px 1px;
        
    }
}
@media (min-width:1200px) {
    .card-list {
        grid-gap: 1px 1px;
        margin-left:8em;
        
    }
}